const eventDate = new Date('2024-01-09')

const timer = setInterval(() => {

    const now = new Date()
    const diff = eventDate - now
    const days = Math.floor(diff / 1000 / 60 / 60 / 24)
    const hours = Math.floor(diff / 1000 / 60 / 60) % 24
    const minutes = Math.floor(diff / 1000 / 60) % 60
    const seconds = Math.floor(diff / 1000) % 60

    document.querySelector('#contador').innerHTML = `${days}d ${hours}h ${minutes}m ${seconds}s`
    console.log(days, hours, minutes, seconds)
    console.log(diff)
    console.log(document.querySelector('#contador'))

    if (diff < 0) {
        clearInterval(timer)
        document.querySelector('#contador').innerHTML = 'Meu aniversÃ¡rio de 16 anos chegou!!!'
    }
})